.back1{
    position:absolute;
    right: 0;
    background-size:100% 100%;
    background-repeat:no-repeat;
    width:100%;
    height:100%;
    background-image:url("https://images.unsplash.com/photo-1517817748493-49ec54a32465?ixid=MnwxMjA3fDB8MHxzZWFyY2h8NXx8bm90ZXN8ZW58MHx8MHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60");
  }
  .imageText{
    text-align: center;
    margin-top: 20%;
  }
  .homeh1{
    color: #fff;
    font-size: 50px;
    font-weight: 400;
    background-color: rgba(2, 2, 26, 0.45);
    display: inline-block;
    letter-spacing: 7px;
    padding:0px;
    text-transform: uppercase;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  }
  @media (max-width:576px){
    .homeh1{
      font-size: 30px;
      margin-top:50px;
    }
    .back1{
      background-image:url("https://images.unsplash.com/photo-1481627834876-b7833e8f5570?ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8Ym9va3N8ZW58MHx8MHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60");
    }
    .homeh6{
      margin-bottom: 50px;
    }
  }
  .homeh6{
    color: #fff;
    font-size:20px;
    font-weight: 400;
    margin-top: 20px;
    background-color: rgba(2, 2, 26, 0.80);
    display: inline-block;
    letter-spacing: 3px;
    padding:0px;
  }